'use client';

import React from 'react';
import styled from 'styled-components';
import { motion } from 'framer-motion';
import { Download, Globe, Mail, Phone, MapPin, Printer, Share2, ShieldCheck, Layers } from 'lucide-react';
import { Container, Section } from '@/components/ui/Container';
import { Navbar } from '@/components/layout/Navbar';
import { Footer } from '@/components/layout/Footer';

const PageHero = styled.div`
  padding: 12rem 0 4rem;
  background: var(--accent);
  text-align: center;
`;

const ResumePaper = styled(motion.div)`
  background: white;
  max-width: 1000px;
  margin: -4rem auto 8rem;
  padding: 5rem;
  box-shadow: var(--shadow-xl);
  border: 1px solid var(--border);
  border-radius: 2rem;
  position: relative;
  z-index: 10;
  
  @media (max-width: 768px) {
    padding: 2rem;
    margin: -2rem 1rem 6rem;
  }
`;

const ResumeHeader = styled.div`
  border-bottom: 3px solid var(--primary);
  padding-bottom: 3rem;
  margin-bottom: 3rem;
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  
  @media (max-width: 768px) {
    flex-direction: column;
    gap: 2rem;
  }
`;

const ContactGrid = styled.div`
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 1.5rem;
  
  .item {
    display: flex;
    align-items: center;
    gap: 0.75rem;
    font-size: 0.95rem;
    color: var(--secondary);
    font-weight: 600;
  }
`;

const SectionTitle = styled.h2`
  font-size: 1.4rem;
  text-transform: uppercase;
  letter-spacing: 2px;
  color: var(--primary);
  border-left: 4px solid var(--primary);
  padding-left: 1.5rem;
  margin-bottom: 2rem;
  display: flex;
  align-items: center;
  gap: 1rem;
`;

const ExpItem = styled.div`
  margin-bottom: 3rem;
  position: relative;
  
  &::before {
    content: '';
    position: absolute;
    left: -2.5rem;
    top: 5px;
    width: 12px;
    height: 12px;
    background: var(--primary-light);
    border-radius: 50%;
  }

  .title-row {
    display: flex;
    justify-content: space-between;
    margin-bottom: 0.5rem;
    
    h3 { font-size: 1.25rem; font-weight: 800; }
    span { font-weight: 700; color: var(--primary); }
  }
  
  .company {
    font-weight: 700;
    color: var(--secondary);
    font-size: 1.05rem;
    margin-bottom: 1rem;
    display: block;
  }
  
  ul {
    list-style: none;
    li {
      margin-bottom: 0.75rem;
      color: #334155;
      line-height: 1.6;
      display: flex;
      gap: 1rem;
      
      &::before { content: '•'; color: var(--primary); font-weight: 900; }
    }
  }
`;

const SkillBadgeGrid = styled.div`
  display: flex;
  flex-wrap: wrap;
  gap: 0.75rem;
  
  span {
    background: var(--accent);
    color: var(--primary-dark);
    padding: 0.5rem 1rem;
    border-radius: 8px;
    font-weight: 700;
    font-size: 0.85rem;
    border: 1px solid var(--border);
  }
`;

const FloatingActions = styled.div`
  position: fixed;
  bottom: 2.5rem;
  right: 2.5rem;
  display: flex;
  flex-direction: column;
  gap: 1rem;
  z-index: 100;
`;

const ActionBtn = styled.button`
  width: 60px;
  height: 60px;
  background: white;
  color: var(--primary);
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  box-shadow: var(--shadow-lg);
  border: 1px solid var(--border);
  
  &:hover {
    background: var(--primary);
    color: white;
    transform: scale(1.1);
  }
`;

import { useQuery } from '@tanstack/react-query';

export default function ResumePage() {
  const { data: about, isLoading: loadingAbout } = useQuery({
    queryKey: ['about'],
    queryFn: () => fetch('/api/data?type=about').then(res => res.json())
  });

  const { data: experience, isLoading: loadingExperience } = useQuery({
    queryKey: ['experience'],
    queryFn: () => fetch('/api/data?type=experience').then(res => res.json())
  });

  const { data: skills, isLoading: loadingSkills } = useQuery({
    queryKey: ['skills'],
    queryFn: () => fetch('/api/data?type=skills').then(res => res.json())
  });

  if (loadingAbout || loadingExperience || loadingSkills) return null;

  return (
    <>
      <Navbar />
      <PageHero>
        <Container>
          <motion.div initial={{ opacity: 0 }} animate={{ opacity: 1 }}>
            <span style={{ background: 'white', padding: '0.4rem 1rem', borderRadius: '100px', fontSize: '0.8rem', fontWeight: 800, color: 'var(--primary)', border: '1px solid var(--border)' }}>
              ATS OPTIMIZED DOCUMENT
            </span>
            <h1 style={{ fontSize: '3.5rem', fontWeight: 900, marginTop: '1.5rem', letterSpacing: '-1.5px' }}>
              Professional <span style={{ color: 'var(--primary)' }}>Curriculum</span>
            </h1>
          </motion.div>
        </Container>
      </PageHero>

      <Container>
        <ResumePaper
          initial={{ opacity: 0, y: 50 }}
          animate={{ opacity: 1, y: 0 }}
          transition={{ duration: 0.8, ease: "easeOut" }}
        >
          <ResumeHeader>
            <div>
              <h1 style={{ fontSize: '3rem', fontWeight: 900, marginBottom: '0.5rem', letterSpacing: '-1px' }}>{about.name}</h1>
              <h3 style={{ color: 'var(--secondary)', fontSize: '1.4rem', fontWeight: 600 }}>{about.title}</h3>
            </div>
            <ContactGrid>
              <div className="item"><Mail size={18} /> {about.email}</div>
              <div className="item"><Phone size={18} /> {about.phone}</div>
              <div className="item"><Globe size={18} /> {about.name.toLowerCase().replace(' ', '')}.dev</div>
              <div className="item"><MapPin size={18} /> {about.location}</div>
            </ContactGrid>
          </ResumeHeader>

          <div style={{ marginBottom: '4rem' }}>
            <SectionTitle><ShieldCheck size={24} /> Professional Summary</SectionTitle>
            <p style={{ fontSize: '1.1rem', lineHeight: '1.8', color: '#334155' }}>
              {about.bio}
            </p>
          </div>

          <div style={{ marginBottom: '4rem', paddingLeft: '2.5rem', borderLeft: '2px dashed var(--border)' }}>
            <SectionTitle style={{ marginLeft: '-2.7rem' }}><Printer size={24} /> Experience</SectionTitle>

            {experience.map((exp: any, i: number) => (
              <ExpItem key={i}>
                <div className="title-row">
                  <h3>{exp.role}</h3>
                  <span>{exp.period}</span>
                </div>
                <span className="company">{exp.company}</span>
                <ul>
                  {exp.achievements?.map((a: string, j: number) => (
                    <li key={j}>{a}</li>
                  ))}
                </ul>
              </ExpItem>
            ))}
          </div>

          <div>
            <SectionTitle><Layers size={24} /> Technical Arsenal</SectionTitle>
            <div style={{ display: 'grid', gridTemplateColumns: '1fr 1fr', gap: '3rem' }}>
              <div>
                <h4 style={{ marginBottom: '1rem', fontWeight: 800 }}>Core Technologies</h4>
                <SkillBadgeGrid>
                  {skills.slice(0, 10).map((s: any) => <span key={s.name}>{s.name}</span>)}
                </SkillBadgeGrid>
              </div>
              <div>
                <h4 style={{ marginBottom: '1rem', fontWeight: 800 }}>Primary Focus</h4>
                <SkillBadgeGrid>
                  {skills.slice(10, 20).map((s: any) => <span key={s.name}>{s.name}</span>)}
                </SkillBadgeGrid>
              </div>
            </div>
          </div>
        </ResumePaper>
      </Container>

      <FloatingActions>
        <ActionBtn title="Download PDF"><Download size={24} /></ActionBtn>
        <ActionBtn title="Share Profile"><Share2 size={24} /></ActionBtn>
      </FloatingActions>
      <Footer />
    </>
  );
}
